@import url(https://fonts.googleapis.com/css2?family=Lato&family=Roboto&family=Source+Code+Pro&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin:0;
  padding: 0;
}
.page{
  height:100vh;
  background-image: url(https://product-image.juniqe-production.juniqe.com/media/catalog/product/cache/x386/771/109/771-109-101L.jpg);
  background-size: cover;
  background-position: center;
  display: flex;
  align-items: center;
  justify-content: center;
}
.container{
  height: 400px;
  width:700px;
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 20px;
}
.heading{
  text-align: center;
  margin-top: 1rem;
  font-family: 'Roboto', sans-serif;
}
.tel-box{
  display: flex;
  margin: 1rem;
  margin-top: 0;
}
.select-box{
  margin: 1rem;
  width: 60px;
  height: 40px;
}
.flag-img{
  width: 60px;
  height: 40px;
  position: absolute;
  border: 2px solid black;
  border-radius: 5px;
}
.select-box select{
  font-family: 'Lato', sans-serif;
  width: 60px;
  height: 40px;
  background: none;
  position: absolute;
  border: none;
  font-size: 1rem;
  padding-left: 4rem;
  outline: none;
  -webkit-appearance: none;
}

.tel{
  font-family: 'Lato', sans-serif;
  width: 100%;
  height: 40px;
  background: none;
  border: 2px solid black;
  outline: none;
  margin: 1rem;
  font-size: 1rem;
  padding-left: 1.5rem;
  color: black;
  font-weight: thick;
  border-radius: 5px;
}
.output{
  margin: 1rem;
  margin-top: 0;
  font-size: 1rem;
  font-weight: bold;
  display: flex;
  align-self:center;
  font-family: "Times New Roman",Times, serif;
}
.result{
  font-family: 'Source Code Pro', monospace;
  width:auto;
  padding: 0.25rem;
  border-radius: 5px;
  height: 30px;
  background-color: pink;
  color: red;
  font-size: 1.5rem;
  margin-left: 1rem;

}
